import React, { useState } from "react";
import { motion } from "framer-motion";
import { Plus, Clock, Edit2, Trash2 } from "lucide-react";

const remindersData = [
  { id: 1, time: "08:00 AM", medicine: "Paracetamol", dosage: "500mg", frequency: "Daily" },
  { id: 2, time: "02:00 PM", medicine: "Amoxicillin", dosage: "250mg", frequency: "Twice a Day" },
  { id: 3, time: "09:00 PM", medicine: "Vitamin D", dosage: "1000 IU", frequency: "Daily" }
];

export default function MedicineReminder() {
  const [reminders] = useState(remindersData);
  const [selectedDate, setSelectedDate] = useState(new Date());

  const addReminder = () => {
    // Placeholder for adding a reminder
    alert("Add reminder functionality coming soon!");
  };

  return (
    <div className="min-h-screen bg-gray-100 p-6">
      {/* Header */}
      <div className="bg-orange-500 text-white text-center py-3 text-xl font-bold shadow-lg rounded-md">
        MediAI - Medicine Reminder
      </div>

      {/* Date Selector */}
      <div className="flex justify-between items-center mt-4 p-4 bg-white rounded-md shadow-md">
        <button onClick={() => setSelectedDate(new Date(selectedDate.setDate(selectedDate.getDate() - 1)))}>
          ◀
        </button>
        <div className="text-lg font-semibold">{selectedDate.toDateString()}</div>
        <button onClick={() => setSelectedDate(new Date(selectedDate.setDate(selectedDate.getDate() + 1)))}>
          ▶
        </button>
      </div>

      {/* Reminders List */}
      <div className="mt-4">
        {reminders.length === 0 ? (
          <p className="text-center text-gray-600">No reminders for today</p>
        ) : (
          reminders.map((reminder) => (
            <motion.div
              key={reminder.id}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4 }}
              className="flex items-center justify-between p-4 mb-2 bg-white rounded-lg shadow-md"
            >
              <div className="flex items-center gap-3">
                <Clock className="h-6 w-6 text-orange-500" />
                <div>
                  <p className="text-lg font-semibold">{reminder.time}</p>
                  <p className="text-sm text-gray-500">{reminder.medicine} - {reminder.dosage}</p>
                </div>
              </div>
              <div className="flex gap-2">
                <button className="p-2 bg-blue-500 text-white rounded-md hover:bg-blue-600">
                  <Edit2 size={18} />
                </button>
                <button className="p-2 bg-red-500 text-white rounded-md hover:bg-red-600">
                  <Trash2 size={18} />
                </button>
              </div>
            </motion.div>
          ))
        )}
      </div>

      {/* Add Reminder Button */}
      <div className="fixed bottom-6 right-6">
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          className="flex items-center gap-2 px-4 py-2 bg-green-500 text-white font-semibold rounded-full shadow-md hover:bg-green-600"
          onClick={addReminder}
        >
          <Plus size={20} /> Add Reminder
        </motion.button>
      </div>
    </div>
  );
}
