import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route, Routes, useNavigate } from "react-router-dom";
import { Camera } from "lucide-react";
import { auth, RecaptchaVerifier, signInWithPhoneNumber } from "./firebase";
import { motion } from "framer-motion";
import MedicineReminder from "./MedicineReminder";


const backgroundImage = "/sample_image.webp";

function WelcomeScreen() {
  const navigate = useNavigate();
  return (
    <motion.div
      className="flex flex-col items-center justify-center min-h-screen text-white relative"
      style={{ backgroundImage: `url(${backgroundImage})`, backgroundSize: "cover" }}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      <div className="absolute top-0 left-0 w-full bg-gray-800 text-white text-center py-0.2 text-1xl font-bold tracking-wide font-serif shadow-md">
        MedAI
      </div>
      <motion.div
        className="bg-black bg-opacity-50 p-6 rounded-lg shadow-lg text-center"
        initial={{ y: 50 }}
        animate={{ y: 0 }}
        transition={{ type: "spring", stiffness: 100 }}
      >
        <h1 className="text-xl font-bold">Welcome to MedAI</h1>
        <p className="mt-2">Your smart medicine verification app</p>
        <motion.button
          onClick={() => navigate("/register")}
          className="mt-4 px-4 py-0.5 bg-white text-gray-700 rounded-lg overflow-hidden relative"
          whileHover={{ scale: 1.05 }}
        >
          Click To Continue
        </motion.button>
      </motion.div>
    </motion.div>
  );
}

function RegisterPage() {
  const [phone, setPhone] = useState("");
  const [otp, setOtp] = useState("");
  const [otpSent, setOtpSent] = useState(false);
  const [confirmationResult, setConfirmationResult] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    if (!window.recaptchaVerifier) {
      window.recaptchaVerifier = new RecaptchaVerifier(auth, "recaptcha-container", { size: "normal" });
      window.recaptchaVerifier.render();
    }
    
    return () => {
      window.recaptchaVerifier = null;
    };
  }, []);

  const sendOtp = async () => {
    if (phone.length === 10) {
      try {
        const formattedPhone = `+91${phone}`;
        const confirmation = await signInWithPhoneNumber(auth, formattedPhone, window.recaptchaVerifier);
        setConfirmationResult(confirmation);
        setOtpSent(true);
        alert("OTP sent successfully!");
      } catch (error) {
        alert("Failed to send OTP!");
      }
    } else {
      alert("Please enter a valid phone number!");
    }
  };

  const verifyOtp = async () => {
    if (!confirmationResult) {
      alert("OTP verification failed. Try resending OTP.");
      return;
    }
    try {
      await confirmationResult.confirm(otp);
      alert("OTP verified successfully!");
      navigate("/upload");
    } catch (error) {
      alert("Invalid OTP! Try again.");
    }
  };

  return (
    <motion.div
      className="flex flex-col items-center justify-center min-h-screen bg-cover bg-center relative"
      style={{ backgroundImage: `url(${backgroundImage})` }}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      <div className="absolute top-0 left-0 w-full bg-gray-800 text-white text-center py-0.2
       text-1xl font-bold tracking-wide font-serif shadow-md">
        MedAI
      </div>
      <div className="bg-white p-6 shadow-md rounded-md text-center max-w-xs">
        <h2 className="text-lg font-bold">User Registration</h2>
        <input
          type="tel"
          placeholder="Enter your number"
          value={phone}
          onChange={(e) => setPhone(e.target.value.replace(/\D/g, "").slice(0, 10))}
          className="w-full border p-1 rounded-md mt-2"
        />
        <div id="recaptcha-container" className="scale-50"></div>
        {!otpSent ? (
          <button onClick={sendOtp} className="w-full bg-blue-500 text-white py-1 mt-2 rounded-md">
            Send OTP
          </button>
        ) : (
          <>
            <input type="text" placeholder="Enter OTP" value={otp} onChange={(e) => setOtp(e.target.value.replace(/\D/g, "").slice(0, 6))}
              className="w-full border p-2 mt-2 rounded-md" />
            <button onClick={verifyOtp} className="w-full bg-green-500 text-white py-2 mt-4 rounded-md">
              Verify OTP
            </button>
          </>
        )}
      </div>
    </motion.div>
  );
}

function UploadPage() {
  const [image, setImage] = useState(null);
  const navigate = useNavigate(); // Hook for navigation

  const handleImageCapture = (event) => {
    const file = event.target.files[0];
    if (file) {
      setImage(URL.createObjectURL(file));
    }
  };

  return (
    <motion.div
      className="flex flex-col items-center justify-center min-h-screen bg-cover bg-center relative"
      style={{ backgroundImage: `url(${backgroundImage})` }}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 1 }}
    >
      {/* Header */}
      <div className="absolute top-0 left-0 w-full bg-gray-600 text-white text-center py-0.2 text-1xl font-bold tracking-wide font-serif shadow-md">
        MedAI
      </div>

      {/* Upload Box */}
      <div className="bg-white p-6 shadow-lg rounded-lg text-center max-w-xs scale-75">
        <h2 className="text-lg font-bold">Upload Prescription</h2>
        <label className="flex flex-col items-center p-2 border-2 border-dashed cursor-pointer mt-4 rounded-lg">
          <Camera className="h-6 w-6 text-gray-500" />
          <span className="text-gray-500 text-sm mt-2">Capture or Upload Image</span>
          <input type="file" accept="image/*" capture="environment" className="hidden" onChange={handleImageCapture} />
        </label>

        {image && (
          <img src={image} alt="Prescription" className="mt-4 rounded-lg shadow-md max-w-full h-auto" />
        )}
        <button onClick={() => navigate("/reminders")} className="mt-4 px-4 py-2 bg-green-500 text-white rounded-lg">
        Set Medicine Reminders
      </button>
    </div>
  </motion.div>
  );
}

export default function App() {
  const [showWelcome, setShowWelcome] = useState(true);

  return (
    <Router>
      {showWelcome ? (
        <WelcomeScreen onContinue={() => setShowWelcome(false)} />
      ) : (
        <Routes>
          <Route path="/" element={<RegisterPage />} />
          <Route path="/register" element={<RegisterPage />} />
          <Route path="/upload" element={<UploadPage />} />
          <Route path="/reminders" element={<MedicineReminder />} />
        </Routes>
      )}
    </Router>
  );
}

